/* global ga, window */
import React, { Component } from 'react';
import './App.css';
import SwipeableViews from 'react-swipeable-views';
import { virtualize } from  'react-swipeable-views-utils';
import Words from './var/words';

import DateView from './DateView';

const StartDate = new Date(2019, 0, 1);


const requiredProps = ['word', 'type', 'defn', 'usage']


const VirtualizeSwipeableViews = virtualize(SwipeableViews);


function isValidWord (word) {
	for (var i = 0; i < requiredProps.length; i++) {
		if (!word[requiredProps[i]]) {
			return false
		}
		
	}
	return true;
}

const ValidWords = Words.filter(isValidWord);
console.log('Valid Word Count:', ValidWords.length);
// const days = [{
// 	date: new Date(),
// 	word: ValidWords[ValidWords.length - 1]
// }];



var DayOffset = 0;
const days = (function () {
	var date = StartDate;
	var now = Date.now();
	var out = [];
	while (date.getTime() < now) {
		DayOffset++;
	// while (out.length < Words.length) {
		out.push({
			date: new Date(date),
			word: ValidWords[out.length]
		});
		date.setDate(date.getDate() + 1);
	}
	return out;
}())


function renderSlide ({ key, index }) {
	console.log('renderslide', key, index);
	if (index > 0) {
		return (
			<div key={ key } className="come-back">
				Come back tomorrow!
			</div>
		);
	}
	const day = days[days.length - 1 + index];
	if (!day) {
		return (
			<div key={ key } className="come-back">
				No word (yet)
			</div>
		);
	}
	return (
		<div key={ key }>
			<DateView { ...day  }/>
		</div>
	);
}


const pad = n => ((n < 10) ? '0' : '') + n;

function doTrack (index) {
	if (!!window.location.port) {
		return;
	}
	const day = days[days.length - 1 + index];
	if (day) {
		ga('send', 'pageview', `/wotd/${ day.date.getFullYear() }-${ pad(day.date.getMonth() + 1) }-${ pad(day.date.getDate()) }`)
	} else {
		ga('send', 'pageview', '/wotd/unknown')
	}
}
doTrack(0);
class App extends Component {
	switchDays () {

	}
	render () {
		return (
			<div className="app">
				<VirtualizeSwipeableViews
					slideRenderer={ renderSlide }
					onChangeIndex={ doTrack }
				/>
			</div>
		);
	}
}
	
export default App;
	
